.wpte-product-container {
    .wpte-product-hover-style-caption-overflow {
        overflow: visible;
    }

    .wpte-caption-layout-wishlist-icon{
        position: absolute;
        top: 0;
        right: 0;
    }

    .wpte-product-caption-hover-style-3{
        &,
        &:before,
        &:after,
        & .wpte-product-hover-figure,
        & .wpte-product-hover-figure:before,
        & .wpte-product-hover-figure:after,
        & .wpte-product-hover-figure-caption,
        & .wpte-product-hover-figure-caption:before,
        & .wpte-product-hover-figure-caption:after {
            background-color: rgba(205, 11, 219, 1);
        }
    }

    [class^='wpte-product-book-open-'] *,
    [class*=' wpte-product-book-open-'] * {
        overflow: visible !important;
    }

    *[class^='wpte-product-book-open-'] .wpte-product-hover-figure,
    *[class*=' wpte-product-book-open-'] .wpte-product-hover-figure,
    *[class^='wpte-product-book-open-'] .wpte-product-hover-figure-caption,
    *[class*=' wpte-product-book-open-'] .wpte-product-hover-figure-caption {
        background-color: transparent !important;
        background: transparent !important;
        border: none;
    }

    *[class^='wpte-product-book-open-'] .wpte-product-hover-figure-caption:before,
    *[class^='wpte-product-book-open-'] .wpte-product-hover-figure-caption:after,
    *[class*=' wpte-product-book-open-'] .wpte-product-hover-figure-caption:before,
    *[class*=' wpte-product-book-open-'] .wpte-product-hover-figure-caption:after {
        z-index: -1;
        content: '';
        position: absolute;
        opacity: 0;
        transition: all 0.45s ease;
    }

    *[class^='wpte-product-book-open-']:hover .wpte-product-hover-figure-caption:before,
    *[class^='wpte-product-book-open-']:hover .wpte-product-hover-figure-caption:after,
    *[class*=' wpte-product-book-open-']:hover .wpte-product-hover-figure-caption:before,
    *[class*=' wpte-product-book-open-']:hover .wpte-product-hover-figure-caption:after {
        opacity: 1;
        transform: rotateY(0deg);
    }

    .wpte-product-book-open-horizontal {
        * {
            perspective: 50em;
        }
        .wpte-product-hover-figure-caption {
            * {
                opacity: 0;
            }
            &:before,
            &:after{
                height: 100%;
                width: 50%;
                top: 0;
            }
            &:before{
                background-image: linear-gradient(to right, transparent 60%, rgba(0, 0, 0, 0.1) 99%, rgba(0, 0, 0, 0.15) 100%);
                left: 0;
                transform: rotateY(90deg);
                transform-origin: 100% 50%;
            }
            &:after {
                background-image: linear-gradient(to left, transparent 60%, rgba(0, 0, 0, 0.12) 99%, rgba(0, 0, 0, 0.15) 100%);
                right: 0;
                transform: rotateY(-90deg);
                transform-origin: 0% 50%;
            }
        }

        &:hover{
            &,
            .wpte-product-hover-figure-caption,
            .wpte-product-hover-figure{
                background: transparent !important;
            }
            .wpte-product-hover-figure-caption{
                *{
                    opacity: 1;
                    transition-delay: 0.21s;
                }
            }
        }
    }

    .wpte-product-book-open-vertical{
        * {
            perspective: 50em;
        }
        .wpte-product-hover-figure-caption{
            * {
                opacity: 0;
            }
            &:before,
            &:after{
                height: 50%;
                width: 100%;
                left: 0; 
            }
            &:before {
                background-image: linear-gradient(to bottom, transparent 60%, rgba(0, 0, 0, 0.1) 99%, rgba(0, 0, 0, 0.15) 100%);
                top: 0;
                transform: rotateX(-90deg);
                transform-origin: 50% 100%;
            }
            &:after {
                background-image: linear-gradient(to top, transparent 60%, rgba(0, 0, 0, 0.13) 99%, rgba(0, 0, 0, 0.15) 100%);
                bottom: 0;
                transform: rotateX(90deg);
                transform-origin: 50% 0%;
            }
        }
        &:hover{
            &,
            .wpte-product-hover-figure-caption,
            .wpte-product-hover-figure{
                background: transparent !important;
            }
            .wpte-product-hover-figure-caption{
                * {
                    opacity: 1;
                    transition-delay: 0.21s;
                }
            }
        } 
    }

    .wpte-product-book-open-up {
        * {
            perspective: 50em;
        }

        .wpte-product-hover-image {
            transform-origin: 50% 0%;
        }
        .wpte-product-hover-figure-caption {
            z-index: -1;
            * {
                opacity: 1;
            }
            &:after {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-image: linear-gradient(to top, transparent 80%, rgba(0, 0, 0, 0.2) 99%, rgba(0, 0, 0, 0.3) 100%);
                left: 0;
            }
        }
        &:hover{
            .wpte-product-hover-image{
                transform: rotateX(86deg);
            }
        }
    }


    .wpte-product-book-open-left{
        * {
            perspective: 50em;
        }
        .wpte-product-hover-image {
            transform-origin: 0% 50%;
        }
        .wpte-product-hover-figure-caption {
            z-index: -1;
            * {
                opacity: 1;
            }
            &:after {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-image: linear-gradient(to left, transparent 80%, rgba(0, 0, 0, 0.2) 99%, rgba(0, 0, 0, 0.3) 100%);
                left: 0;
            }
        }
        &:hover{
            .wpte-product-hover-image{
                transform: rotateY(-86deg);
            }
        }
    }

    .wpte-product-book-open-right{
        * {
            perspective: 50em;
        }
        .wpte-product-hover-image {
            transform-origin: 100% 50%;
        }
        .wpte-product-hover-figure-caption {
            z-index: -1;
            * {
                opacity: 1;
            }
            &:after {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-image: linear-gradient(to right, transparent 80%, rgba(0, 0, 0, 0.2) 99%, rgba(0, 0, 0, 0.3) 100%);
                left: 0;
            }
        }
        &:hover{
            .wpte-product-hover-image{
                transform: rotateY(86deg);
            }
        }
    }

    .wpte-product-book-open-down{
        * {
            perspective: 50em;
        }
        .wpte-product-hover-image {
            transform-origin: 50% 100%;
        }
        .wpte-product-hover-figure-caption {
            z-index: -1;
            * {
                opacity: 1;
            }
            &:after {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-image: linear-gradient(to bottom, transparent 80%, rgba(0, 0, 0, 0.2) 99%, rgba(0, 0, 0, 0.3) 100%);
                left: 0;
            }
        }
        &:hover{
            .wpte-product-hover-image{
                transform: rotateX(-86deg);
            }
        }
    }
}